<template>
  <div>
    <el-dialog class="dialog_Div" @close="closeDialog" 
        title="编辑信息" :visible.sync="this.$store.state.dialog.dialogOpen" width="75%">
      <div>
        <el-form :model="editFrom" label-width="75px" ref="editFrom" label-position="center">
          <!-- 第一行 -->
          <el-row :gutter="10">
             <el-col :span="6">
                <el-form-item label="参数:" >
                   <el-select v-model="editFrom.type_id" placeholder="选择分类"  allow-create filterable>
                    <el-option v-for="item in typeListMv" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                 </el-form-item>
             </el-col>
             <el-col :span="4">
                <el-select v-model="editFrom.vod_level" placeholder="选择推荐" > 
                  <el-option v-for="item in tuijianOptions" :key="item.label" :label="item.label" :value="item.value"></el-option>
                </el-select>
             </el-col>
             <el-col :span="4">
               <el-select v-model="editFrom.vod_status" placeholder="选择状态" clearable>
                   <el-option v-for="item in statusOption" :key="item.label" :label="item.label" :value="item.status"></el-option>
               </el-select>
             </el-col>
             <el-col :span="4">
                <el-select v-model="editFrom.vod_lock" placeholder="选择锁定" clearable  >
                  <el-option v-for="item in lockOption" :key="item.label" :label="item.label" :value="item.status"></el-option>
                </el-select>
             </el-col>
             <el-col :span="4">
                <el-select v-model="editFrom.vod_isend" placeholder="选择完结" clearable>
                  <el-option v-for="item in isendOption" :key="item.label" :label="item.label" :value="item.status"></el-option>
                </el-select>
             </el-col>
          </el-row>
          <!-- 第二行 -->
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="名称:">
                <el-input class="input_class" v-model="editFrom.vod_name"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="TAG:">
                <el-input class="input_class" v-model="editFrom.vod_tag"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
           <!-- 第三行 -->
          <el-row :gutter="10" class="two_line">
            <el-col :span="6">
             <el-form-item label="语言:">
                <el-select v-model="editFrom.vod_lang" placeholder="选择语言" clearable allow-create filterable>
                    <el-option v-for="item in langOption" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="地区:">
                <el-select v-model="editFrom.vod_area" placeholder="选择地区" clearable allow-create filterable>
                    <el-option v-for="item in areaList" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <!-- <el-col :span="8">
              <el-form-item label="上映日期:">
                <el-date-picker
                  type="date"
                  placeholder="选择日期"
                  v-model="editFrom.vod_time"
                  value-format="yyyy-MM-dd"
                  style="width: 100%"
                >
                </el-date-picker>
              </el-form-item>
            </el-col> -->

            
          </el-row>
          <!-- 第四行 -->
          <el-row :gutter="10">
            <el-col :span="6">
              <el-form-item label="备注:">
                <el-input class="input_min" v-model="editFrom.vod_remarks"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="年份:">
                <el-input class="input_min" v-model="editFrom.vod_year"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="总集数:">
                 <el-input class="input_min" v-model="editFrom.vod_total"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
             <el-form-item label="连载数:">
                 <el-input class="input_min" v-model="editFrom.vod_serial"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <!-- 第五行 -->
          <el-row :gutter="10">
            <el-col :span="6">
              <el-form-item label="导演:">
                <el-input class="input_min" v-model="editFrom.vod_director"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="编剧:">
                 <el-input class="input_min" v-model="editFrom.vod_writer"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="演员:">
                <el-input class="input_min" v-model="editFrom.vod_actor"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <!-- 第六行 -->
          <el-row :gutter="10">
            <el-col :span="6">
              <el-form-item label="视频时长:">
                <el-input class="input_min" v-model="editFrom.vod_duration"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="拼音:">
                 <el-input class="input_min" v-model="editFrom.vod_writer"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="扩展分类:">
                <el-input class="input_min" v-model="editFrom.vod_class"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="图片上传:">
                <el-input class="input_min" v-model="editFrom.vod_duration"></el-input>
               
              </el-form-item>
            </el-col>
            <el-col :span="2">
             <el-upload
                action=""
                :limit="1"
                list-type="picture"
                :on-exceed="handleExceedMore">
                <el-button  type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-row>
          
          <el-row :gutter="10">
            <el-col :span="24">
               <el-form-item label="简介:">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 8}"
                  placeholder="请输入简介内容"
                  v-model="editFrom.vod_content">
                </el-input>

               </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="24">
               <el-form-item label="播放链接:">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 3, maxRows: 15}"
                  placeholder=""
                  v-model="editFrom.vod_play_url">
                </el-input>

               </el-form-item>
            </el-col>
          </el-row>

        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="editTrue">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script> 
import service from "@/utils/request";
export default {
  name: "Dialog",
  props: ["FromData"],
  data() {
    return {
      dialogOpen: this.$store.state.dialog.dialogOpen,
      editFrom: {},
      typeListMv: [
        {
          value: 1,
          label: "电影",
        },
        {
          value: 2,
          label: "连续剧",
        },
        {
          value: 3,
          label: "综艺",
        },
        {
          value: 4,
          label: "动漫",
        },
        {
          value: 6,
          label: "动作",
        },
        {
          value: 7,
          label: "喜剧",
        },
        {
          value: 8,
          label: "爱情",
        },
        {
          value: 9,
          label: "科幻",
        },
        {
          value: 10,
          label: "恐怖",
        },
        {
          value: 11,
          label: "剧情",
        },
        {
          value: 12,
          label: "战争",
        },
        {
          value: 13,
          label: "国产",
        },
        {
          value: 14,
          label: "港台",
        },
        {
          value: 15,
          label: "日韩",
        },
        {
          value: 16,
          label: "欧美",
        },
        {
          value: 20,
          label: "惊悚",
        },
        {
          value: 21,
          label: "犯罪",
        },
        {
          value: 22,
          label: "冒险",
        },
        {
          value: 23,
          label: "悬疑",
        },
        {
          value: 24,
          label: "动画",
        },
        {
          value: 25,
          label: "武侠",
        },
        {
          value: 26,
          label: "奇幻",
        },
        {
          value: 27,
          label: "纪录",
        },
        {
          value: 31,
          label: "影视资讯",
        },
        {
          value: 33,
          label: "八卦",
        },
        {
          value: 34,
          label: "头条",
        },
      ],
      tuijianOptions: [
        { value: 0, label: "推荐0"},
        { value: 1, label: "推荐1" },
        { value: 2, label: "推荐2" },
        { value: 3, label: "推荐3" },
        { value: 4, label: "推荐4" },
        { value: 5, label: "推荐5" },
        { value: 6, label: "推荐6" },
        { value: 7, label: "推荐7" },
        { value: 8, label: "推荐8" },
        { value: 9, label: "推荐9" },
      ],
      statusOption: [
        { status: 0, label: "正常" },
        { status: 1, label: "异常" },
      ],
      langOption: [
        { value: "英语", label: "英语" },
        { value: "国语", label: "国语" },
        { value: "粤语", label: "粤语" },
        { value: "闽南语", label: "闽南语" },
        { value: "日语", label: "日语" },
        { value: "法语", label: "法语" },
        { value: "德语", label: "德语" },
        { value: "其他", label: "其他" },
      ],
      areaList: [
        { value: "中国大陆", label: "中国大陆" },
        { value: "中国香港", label: "中国香港" },
        { value: "中国台湾", label: "中国台湾" },
        { value: "美国", label: "美国" },
        { value: "法国", label: "法国" },
        { value: "英国", label: "英国" },
        { value: "日本", label: "日本" },
        { value: "韩国", label: "韩国" },
        { value: "德国", label: "德国" },
        { value: "印度", label: "印度" },
        { value: "西班牙", label: "西班牙" },
      ],
      lockOption: [
        { status: 1, label: "锁定" },
        { status: 0, label: "解锁" },
      ],
      isendOption: [
        { status: 1, label: "完结" },
        { status: 0, label: "未完结" },
      ],
      imgOption: [
        { status: 1, label: "无图片" },
        { status: 0, label: "有图片" },
      ],
    };
  },
  methods: {
    editTrue() {
      //  console.log('fason:',this.editFrom);
       this.InsertOrUpt(this.$store.state.dialog.method); //更新
       console.log('fangshi:',this.$store.state.dialog.method);
    },
    InsertOrUpt(val){
      let met='';
      let msg='';
      if(val=='upt'){
        met='upt';
        msg="修改";
      }else{
        met='add';
        msg="添加";
      }
       service
      .post("/admin/video", { 
          method: met,
          edit:this.editFrom,
       })
      .then((result) => {
        console.log("result",result);
        try {
          if(result.data[0].insertId!=null){
            this.$message({
              message: msg+'成功',
              type: 'success'
            });
            this.$store.commit("dialogClose");
          }else{
            this.$message.error('操作失败');
          }
        } catch (error) {
            console.log(error);
        }
        
        
      })
      .catch((err) => {
        console.log(err);
        this.$message.error('操作失败');
      });
      
    },
    closeDialog(){
      console.log('close');
      this.$store.commit("dialogClose");

    },
    handleExceedMore(){
      console.log('文件超出个数限制');
    }
  },
  watch:{
      FromData: {
          handler: function() {
            if (this.FromData) {
              this.editFrom =this.FromData;
            }
          },
          deep: true,
          immediate: true
      },
  },
  mounted() {
    this.editFrom =this.FromData;
  },
};
</script>

<style scoped>


.input_class{
  width:100%;
}
.two_line .el-select{
  margin: 0;
}
.input_min{
  width: 100%;
}
</style>