<template>
  <div>
    <!-- 选择项 -->
    <div class="typeSelectDiv">
      <el-select v-model="typeValue" placeholder="选择分类" clearable >
        <el-option v-for="item in typeListMv" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <el-select v-model="tuijianValue" placeholder="选择推荐" clearable > 
        <el-option v-for="item in tuijianOptions" :key="item.label" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <el-select v-model="areaValue" placeholder="选择地区" clearable >
         <el-option v-for="item in areaList" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <el-select v-model="langValue" placeholder="选择语言" clearable >
          <el-option v-for="item in langOption" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <el-select v-model="statusValue" placeholder="选择状态" clearable >
        <el-option v-for="item in statusOption" :key="item.label" :label="item.label" :value="item.status"></el-option>
      </el-select>
       <el-select v-model="lockValue" placeholder="选择锁定" clearable >
        <el-option v-for="item in lockOption" :key="item.label" :label="item.label" :value="item.status"></el-option>
      </el-select>
       <el-select v-model="isendValue" placeholder="选择完结" clearable >
        <el-option v-for="item in isendOption" :key="item.label" :label="item.label" :value="item.status"></el-option>
      </el-select>
      <el-select v-model="imgValue" placeholder="选择图片" clearable >
        <el-option v-for="item in imgOption" :key="item.label" :label="item.label" :value="item.status"></el-option>
      </el-select>
      <el-select v-model="playerUrlValue" placeholder="选择播放地址" clearable >
        <el-option v-for="item in playerUrlOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
       <el-select v-model="sortValue" placeholder="选择排序" clearable >
          <el-option v-for="item in sortList" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <el-input class="inp" v-model="inputValue" placeholder="请输入内容" clearable></el-input>
      <el-button class="btn" icon="el-icon-search" @click="search" >搜索</el-button>
      <el-button class="btn" icon="el-icon-plus" @click="addVideo" >添加</el-button>
      <el-button class="btn" icon="el-icon-delete" @click="delVideo" >删除</el-button>
      <el-button class="btn" icon="el-icon-refresh-left" @click="shuaXin" ></el-button>
    </div>
    <!-- 表格 -->
    <div>
      <el-table  
          ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%"
            @selection-change="handleSelectionChange" border  >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="编号" width="90" align="center">
            <template slot-scope="scope">{{ scope.row.vod_id }}</template>
          </el-table-column>
          <el-table-column prop="vod_id" label="名称">
            <template slot-scope="scope">
               <el-tag class="tag">{{scope.row.vod_year}}</el-tag>
               <router-link :to="`/detail?id=${scope.row.vod_id}`" class="vod_name" target="_blank">
               {{ scope.row.vod_name }}
               </router-link>
               <el-tag type="warning"> {{ scope.row.vod_remarks }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="人气" width="70" align="center">
              <template slot-scope="scope">{{ scope.row.vod_hits }}</template>
          </el-table-column>
          <el-table-column label="类型" width="150">
             <template slot-scope="scope">{{ scope.row.vod_class }}</template>
          </el-table-column>
          <el-table-column label="平均分" width="65" align="center">
             <template slot-scope="scope">{{ scope.row.vod_score }}.0</template>
          </el-table-column>
          <el-table-column label="推荐" width="55" align="center">
            <template slot-scope="scope">
              <el-tag type="primary">{{ scope.row.vod_level }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="状态" width="55"> 
            <template slot-scope="scope">
              <!-- {{scope.row.vod_status}} -->
              <el-tag v-show="scope.row.vod_status == 1" type="success">Y</el-tag>
              <el-tag v-show="scope.row.vod_status == 0" type="danger">N</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="更新时间" width="130">
            <template slot-scope="scope">{{ scope.row.vod_time_add }}</template>
          </el-table-column>

          <el-table-column label="操作"  width="160">
           <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
           </template>
          </el-table-column>
         
        </el-table>
        <!-- <div style="margin-top: 20px">
          <el-button @click="toggleSelection([tableData[1], tableData[2]])">切换第二、第三行的选中状态</el-button>
          <el-button @click="toggleSelection()">取消选择</el-button>
        </div> -->
    </div>

    <!-- 分页 -->
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[20, 30, 40, 50]"
        :page-size="20"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>


    <!-- 添加框 -->
    <div>
      <Dialog :FromData='DialogData' ></Dialog>
      
    </div>
  </div>
</template>

<script>
import service from "@/utils/request";
import Dialog from '@/components/Dialog';
export default {
  name: "videoData",
  components:{Dialog},
  data() {
    return {
      oldTableData:[],
      tableData: [],
      inputValue:'',
      typeValue:'',
      typeOptions:'',
      tuijianValue:'',
      statusValue:'',
      areaValue:'',
      langValue:'',
      sortValue:'',
      lockValue:'',
      isendValue:'',
      imgValue:'',
      playerUrlValue:'',
      multipleSelection: [],//multipleSelection 当前选中项
      currentPage: 1,
      typeListMv:[ 
        {
          value: 1,
          label: "电影",
        },
        {
          value: 2,
          label: "连续剧",
        },
        {
          value: 3,
          label: "综艺",
        },
        {
          value: 4,
          label: "动漫",
        },
        {
          value: 6,
          label: "动作",
        },
        {
          value: 7,
          label: "喜剧",
        },
        {
          value: 8,
          label: "爱情",
        },
        {
          value: 9,
          label: "科幻",
        },
        {
          value: 10,
          label: "恐怖",
        },
        {
          value: 11,
          label: "剧情",
        },
        {
          value: 12,
          label: "战争",
        },
        {
          value: 13,
          label: "国产",
        },
        {
          value: 14,
          label: "港台",
        },
        {
          value: 15,
          label: "日韩",
        },
        {
          value: 16,
          label: "欧美",
        },
        {
          value: 20,
          label: "惊悚",
        },
        {
          value: 21,
          label: "犯罪",
        },
        {
          value: 22,
          label: "冒险",
        },
        {
          value: 23,
          label: "悬疑",
        },
        {
          value: 24,
          label: "动画",
        },
        {
          value: 25,
          label: "武侠",
        },
        {
          value: 26,
          label: "奇幻",
        },
        {
          value: 27,
          label: "纪录",
        },
        {
          value: 31,
          label: "影视资讯",
        },
        {
          value: 33,
          label: "八卦",
        },
        {
          value: 34,
          label: "头条",
        },
      ],
      tuijianOptions:[
          {value: 0,label: "推荐0"},{value: 1,label: "推荐1"},{value: 2,label: "推荐2"},{value: 3,label: "推荐3"},{value: 4,label: "推荐4"},{value: 5,label: "推荐5"},
          {value: 6,label: "推荐6"},{value: 7,label: "推荐7"},{value: 8,label: "推荐8"},{value: 9,label: "推荐9"},
      ],
      statusOption:[{ status: 0,label: "正常"}, { status: 1,label: "异常"},],
      langOption:[ 
       { value: '英语',label: "英语"}, { value: '国语',label: "国语"}, { value: '粤语',label: "粤语"},
        { value: '闽南语',label: "闽南语"}, { value: '日语',label: "日语"}, { value: '法语',label: "法语"}, { value: '德语',label: "德语"},
        { value: '其他',label: "其他"}],
      areaList:[
        { value: '中国大陆',label: "中国大陆"}, { value: '中国香港',label: "中国香港"},{ value: '中国台湾',label: "中国台湾"},
        { value: '美国',label: "美国"}, { value: '法国',label: "法国"}, { value: '英国',label: "英国"}, { value: '日本',label: "日本"},
        { value: '韩国',label: "韩国"},{ value: '德国',label: "德国"},{ value: '印度',label: "印度"},{ value: '西班牙',label: "西班牙"},],
      lockOption:[{ status: 1,label: "锁定"}, { status: 0,label: "解锁"},],
      isendOption:[{ status: 1,label: "完结"}, { status: 0,label: "未完结"},],
      imgOption:[{ status: 1,label: "无图片"}, { status: 0,label: "有图片"},],
      playerUrlOptions:[],
      sortList:[{ value: 'time',label: "更新时间"}, { value: 'id',label: "编号"},{ value: 'hits',label: "人气"},],
      total:999,//总的条数
      showNum:20, //每页条数
      selectTypeValue:{},
      DialogData:{},
   };
  },
  methods: {
    search(){
      this.currentPage=1;
      let Value={};
      Value.inputValue=this.inputValue;
      Value.typeValue=this.typeValue;
      // Value.typeOptions=this.typeOptions;
      Value.tuijianValue=this.tuijianValue;
      Value.statusValue=this.statusValue;
      Value.areaValue=this.areaValue;
      Value.langValue=this.langValue;
      Value.sortValue=this.sortValue;
      Value.areaValue=this.areaValue;
      Value.lockValue=this.lockValue;
      Value.isendValue=this.isendValue;
      Value.imgValue=this.imgValue;
      Value.playerUrlValue=this.playerUrlValue;
      this.selectTypeValue=Value;
      this.getTabledata();
    },
    addVideo(){
      // console.log('add');
      // this.DialogData={};
      this.$store.commit("dialogShow");
      this.$store.commit("changeMethodAdd");
    },  
    delVideo(){
      console.log('del');
    },
    shuaXin() {
     
      this.search();
    },
    handleEdit(index,row){
      // 编辑
      this.DialogData=row;
      // console.log(this.DialogData);
      this.$store.commit("dialogShowClose");
      this.$store.commit("changeMethodUpt");
    },
    handleDelete(index,row){
      // 删除
      service.post("/admin/video", { method: "del",edit:row.vod_id })
      .then((result) => {
        console.log("result",result.data[0]);
       if(result.data[0].affectedRows==1){  //影响行数
          this.search();
          this.$message({message: "删除成功！",type: "success",});
        }else{
          this.$message.error('删除失败');
        }
      })
      .catch((err) => {
        console.log(err);
        this.$message.error('删除失败');
      });
    },
    toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
    },
    handleSelectionChange(val) {
      // 选中的项
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      // console.log(`每页 ${val} 条`);
      this.showNum=val;
      this.getTabledata();
    },
    handleCurrentChange(val) {
      // 页码
      // console.log(`当前页: ${val}`);
      this.currentPage=val;
      this.getTabledata();
    },
    getTabledata(){
      let typecheck=JSON.stringify(this.selectTypeValue);
      service
      .post("/admin/video", { 
          method: "get",
          page:this.currentPage-1,
          showNum:this.showNum,
          type:typecheck,
        })
      .then((result) => {
        // console.log("total:", result.data[2][0]);
        // console.log("result",result.data[0]);
        this.oldTableData = result.data[0];
        this.tableData = this.oldTableData;  //数据
        this.total=result.data[2][0].total; //页码

      })
      .catch((err) => {
        console.log(err);
        this.$message.error('查询失败');
      });
    },
  },
  mounted() {
    // 第一次进入拿到表格数据
    service
      .post("/admin/video", { method: "get" })
      .then((result) => {
        // console.log("about", result.data[2][0]);
        // console.log("result",result.data[0]);
        
        this.oldTableData = result.data[0];
        this.tableData = this.oldTableData;  //数据
        this.total=result.data[2][0].total; //页码

      })
      .catch((err) => {
        console.log(err);
        this.$message.error('查询失败');
      });
  },
};
</script>

<style scoped>
.typeSelectDiv {
  display: flex;
  justify-self: flex-start;
  flex-wrap: wrap;
}
.el-select {
    margin-right: 5px;
    margin-bottom: 5px;
    width: calc(13% + 10px);
    min-width: 150px;
}
.inp {
    margin-bottom: 5px;
    margin-right: 5px;
    width: calc(26% + 26px);
}
.btn{
  height: 40px;
  margin: 0 5px 0 0px;
  width: calc(7% - 4px);
  min-width: 86px;
}
.block{
  margin-top: 10px;
}
.vod_name{
   text-decoration: none;
   color: #333;
   margin-right: 5px;
   border: 1px solid rgb(233, 233, 233);
   padding: 0 10px;
   height: 32px;
   line-height: 30px;
   border-radius: 4px;
   display: inline-block;
   box-sizing: border-box;
   white-space: nowrap;
}
.tag{
    margin-right: 5px;
}
</style>